import React from "react";

const card = [
  {
    title: "Software Quality Assurance",
    span: "    ,  ",
    txt: [
      "TMMi     ",
      ", , ,  AI, loT,   ",
      "KDLAS   ",
    ],
    keywords: ["SQA", " ", " ", "Test"],
  },
  {
    title: "Smart Solution",
    span: "     /",
    txt: [
      "     'TestFort'",
      "    IMQA",
    ],
    keywords: [
      "  ",
      "IMQA",
      "TestFort",
      "   ",
    ],
  },
  {
    title: "Big Data",
    span: "     ",
    txt: [
      "     'Ankus'",
      "Library, Analyzer, Optimizer,  ",
      "KDLAS   ",
    ],
    keywords: ["  ", "  ", " ", ""],
  },
];
const BusinessBox = () => {
  return (
    <>
      {card.map((item, index) => (
        <div className="content-box" key={index}>
          <div className="text-box">
            <h3>{item.title}</h3>
            <span>{item.span}</span>
            <p>{item.txt[0]}</p>
            <p>{item.txt[1]}</p>
            <p>{item.txt[2] ? item.txt[2] : null}</p>
          </div>
          <hr />
          <div className="keywords-box">
            {item.keywords?.map((k, index) => (
              <p key={index}>{k}</p>
            ))}
          </div>
        </div>
      ))}
    </>
  );
};

export default BusinessBox;
